.popup__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 15px;
  background-color: white;
  border-radius: 10px;
  width: 430px;
  height: fit-content;
  position: fixed;
  margin-inline: auto;
  inset-inline: 0;
  inset-block-start: 50%;
  transform: translateY(-50%);
  z-index: 2;
}

@media (max-width: 600px) {
  .popup__container {
    width: 282px;
  }
}
